import React from 'react'
import './media.css'
import Github from '@iconscout/react-unicons/icons/uil-github'
import LinkedIn from '@iconscout/react-unicons/icons/uil-linkedin'
import WhatsApp from '@iconscout/react-unicons/icons/uil-whatsapp'
import Instagram from '@iconscout/react-unicons/icons/uil-instagram'
import ReactWhatsapp from 'react-whatsapp';

const Media = () => {
    return (
        <div className='media'>
            <a href="https://github.com/simonnava2112">
                <Github color='#042c3f' size='2rem'  />
            </a>
            <a href="https://www.linkedin.com/in/simon-nava-75562714a/">
                <LinkedIn color='#042c3f' size='2rem'/>
            </a>
            <ReactWhatsapp className='whap' number="+54 9 11 2486-8994" message="Hola Simon. Quisiera comunicarme contigo.">
                <WhatsApp color='#042c3f' size='2rem'/>
            </ReactWhatsapp>
            <a href="https://www.instagram.com/simonnava2112/">
                <Instagram color='#042c3f' size='2rem'/>
            </a>
        </div>
    )
}

export default Media