import React from 'react'
import './css/Intro.css'
import Animation from '../img/coding.gif'
import LinkedIn from '@iconscout/react-unicons/icons/uil-linkedin'
import Github from '@iconscout/react-unicons/icons/uil-github'
import Curriculum from '@iconscout/react-unicons/icons/uil-package'
import WhatsApp from '@iconscout/react-unicons/icons/uil-whatsapp'
import ReactWhatsapp from 'react-whatsapp';
import Resume from '../img/simonNavaCv.pdf';


const Intro = () => {
    
    return (
        <div className='intro'>
            
            <div className="i-left">
                <div className="i-name">
                    <span>Hi! I am</span>
                    <span>Simon Nava</span>
                    <span>Frontend Developer with interested in learning and growing in the career of web developer, working to create high quality web pages.</span>

                </div>
            </div>
            <div className="i-right">
                <img className="img-right" src={Animation} alt="" />
                    <div className="i-icon">
                        <a href="https://github.com/simonnava2112">
                            <Github color='white' size='2rem'  />
                        </a>
                        <a href="https://www.linkedin.com/in/simon-nava-75562714a/">
                            <LinkedIn color='white' size='2rem'/>
                        </a>
                        <a href={Resume} download>
                        <Curriculum color='white' size='2rem'/>
                        </a>
                        <ReactWhatsapp className='whap-t' number="+54 9 11 2486-8994" message="Hola Simon. Quisiera comunicarme contigo.">
                            <WhatsApp color='white' size='2rem'/>
                        </ReactWhatsapp>
                    </div>
            </div>

        </div>
    )
}

export default Intro