import React from 'react'
import './Languaje.css'
import Card from './Card'
import java from '../../img/js.png'
import react from '../../img/react-logo.png'
import html from '../../img/html.png'
import css from '../../img/css.png'
import bootstrap from '../../img/bootstraps.png'
import material from '../../img/material-ui.png'

const Languaje = () => {
    return (
        <div className='languaje'>

            <div className="fontend">
                <div className="f-titulo">Frontend Skills</div>
                <div className="all-card">
                    <div className="f-1">
                    <Card img={html} title="HTML5" text="1+ year working."/>
                    <Card img={css} title="CSS3" text="1+ year working."/>
                    <Card img={java} title="JavaScript" text="1 year working."/>
                    </div>
                    <div className="f-1">
                    <Card img={react} title="React" text="1 year working."/>
                    <Card img={bootstrap} title="Bootstraps" text="1 year working."/>
                    <Card img={material} title="Material UI" text="1 year working."/>
                    </div>
                </div>
            </div>

        </div>
    )
}

export default Languaje