import React from 'react'
import './css/Header.css'
import fotoPerfil from '../img/simonnava.jpeg'
import Resume from '../img/simonNavaCv.pdf';
import {Link} from 'react-scroll'

const Header = () => {
    return (
        <div className="navbar">
            <div className="n-left">
                <div className="n-left-img">
                    <img  className="img-perfil" src={fotoPerfil} alt="" />
                </div>
                <a className='n-left-name' href={Resume} download>
                    <div className="n-left-name">
                        Simon Nava
                    <p className='text-right'>Web Developer</p>
                    </div>
                </a>

            </div>
                <ul className="n-right-list">
                    <Link spy={true} to='about' smooth={true} activeClass='activeClass'>
                        <li>About me</li>
                    </Link>
                    <Link spy={true} to='skill' smooth={true} >
                        <li>Skills</li>
                    </Link>
                    <Link spy={true} to='experience' smooth={true} >
                        <li>Experience</li>
                    </Link>
                    <Link spy={true} to='references' smooth={true} >
                        <li>References</li>
                    </Link>
                    <Link spy={true} to='contact' smooth={true} >
                        <li>Contact me</li>
                    </Link>
                </ul>
        </div>
    )
}

export default Header