import React from 'react'
import './css/Experience.css';
import imgPro1 from '../img/mypro1.png'
import imgPro2 from '../img/mypro2.png'
import imgPro3 from '../img/mypro3.png'

const Experience = () => {
    return (
        <div className='experience'>
            <div className="e-info">
                <span>Experience</span>
                <span>Tennis shop</span>
                <span>Ecommerce website for a tennis store that drives sales by bringing products closer to customers.</span>
            </div>
            <div className="pro-1">
                <div className="e-port-1">
                    <img className="img-port-1" src={imgPro1} alt="" />
                    <img className="img-port-1" src={imgPro2} alt="" />
                    <img className="img-port-1" src={imgPro3} alt="" />
                </div>
                <a href="https://wondrous-cascaron-2853e6.netlify.app/">
                    <button className='btn-project'>To Inspect</button>
                </a>
            </div>
        </div>
    )
}

export default Experience