import React from 'react'
import './Card.css'

const Card = ({img, title, text}) => {
    return (
        <div className="card">
            <img  className="l-img" src={img} alt="" />
            <div className="cardtext">
                <span className="l-title">{title}</span>
                <span className="l-text">{text}</span>
            </div>
        </div>
    )
}

export default Card