import React from 'react'
import './Text.css'
import Cole from './Cole'
import udemy from '../../img/udemy.png'
import platzi from '../../img/Platzi.png'


const Text = () => {
    return (
        <div className='text'>
            <Cole img={udemy} texto='With more than 100 hours of studies gained, Udemy has helped me to have knowledge about JavaScript, Nodejs, React(js, native, Redux), Firebase, Bootstraps and Material UI.' />
            <Cole img={platzi} texto='More than 150 hours studied with Platzi obtaining knowledge in HTML, CSS, JavaScript, react, and important aspects that help write cleaner code.' />

        </div>
    )
}

export default Text