import React from 'react'
import './Developer.css'
import img from '../../img/developer.jpg'

const Developer = () => {
    return (
        <div className="developer">
            <div className="foto-d">
                <img className="img-d" src={img} alt="" />
            </div>
            <div className="text-p">
                <span className='d-title'>About me</span>
                <p>Committed and self-motivated frontend developer who can contribute knowledge and studies acquired with a great passion for programming. Always with the determination to complete my objectives in a professional manner, I collaborate with my colleagues to achieve goals together.</p>
                <br/>
                <p>my sports experience helped me acquire a philosophy of teamwork, discipline and desire to learn has helped me to correlate well in the work environment.</p>
            </div>
        </div>

    )
}

export default Developer